body,
html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

#root {
  height: 100%;
  width: 100%;
}

html {
  overflow: hidden;
}

.root {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  background-color: #657b9e;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#wrapper {
  border: 3px solid black;
  height: 550px;
  width: 325px;
  display: -webkit-flex;
  display: flex;
  box-shadow: 0px 0px 30px 0px #aaa;
  
  -webkit-flex-direction: column;
  
          flex-direction: column;
}


.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.button {
  -webkit-flex: 1 1;
          flex: 1 1;
  border-width: thin;
  font-family: "Work Sans", sans-serif;
  font-size: 35px;
  background: #c8c8c8;
  font-weight: bold;
}

.first-row-button {
  background-color: #bebebe;
}

.operator-button {
  background-color: #ff8000;
  border: 0.1px solid black;
  border-bottom: none;
  color: white;
}

.calculator-display {
  color: white;
  background: #1c191c;
  height: 100px;
  text-align: end;
  font-family: "Work Sans", sans-serif;
  font-size: 6em;

  -webkit-flex: 1 1;

          flex: 1 1;
}
.auto-scaling-text {
  display: inline-block;
}

.calculator-display .auto-scaling-text {
  padding: 0 30px;
  -webkit-transform-origin: left;
          transform-origin: left;
}
